.mouse {
	width: 25px;
	height: 45px;
	border: 2px solid rgb(1, 9, 53);
	border-radius: 60px;
	position: relative;

}
  .mouse::before {
		content: '';
		width: 12px;
		height: 12px;
		position: absolute;
		top: 10px;
		left: 50%;
		-webkit-transform: translateX(-50%);
		        transform: translateX(-50%);
		background-color: rgb(13, 139, 212);
		border-radius: 50%;
		opacity: 1;
		animation: wheel 2s infinite;
		-webkit-animation: wheel 2s infinite;
	}


@keyframes wheel {
	to {
		opacity: 0;
		top: 60px;
	}
}

@-webkit-keyframes wheel {
	to {
		opacity: 0;
		top: 60px;
	}
}
.scroll-down {
  position: absolute;
  left: 50%;
  bottom: 20px;
  display: block;
  text-align: center;
  font-size: 30px;
  /* z-index: -999; */
  text-decoration: none;
  text-shadow: 0;
  width: 22px;
  height: 22px;
  border-bottom: 2px solid rgba(6, 0, 31, 0.678);
  border-right: 2px solid rgb(5, 121, 175);
  z-index: 9;
  left: 50%;
  -webkit-transform: translate(-50%, 0%) rotate(45deg);
  transform: translate(-50%, 0%) rotate(45deg);
  -webkit-animation: fade_move_down 2s ease-in-out infinite;
  animation:         fade_move_down 2s ease-in-out infinite;
}


/*animated scroll arrow animation*/
@-webkit-keyframes fade_move_down {
  0%   { -webkit-transform:translate(0,-10px) rotate(45deg); opacity: 0;  }
  50%  { opacity: 1;  }
  100% { -webkit-transform:translate(0,10px) rotate(45deg); opacity: 0; }
}
@keyframes fade_move_down {
  0%   { -webkit-transform:translate(0,-10px) rotate(45deg); transform:translate(0,-10px) rotate(45deg); opacity: 0;  }
  50%  { opacity: 1;  }
  100% { -webkit-transform:translate(0,10px) rotate(45deg); transform:translate(0,10px) rotate(45deg); opacity: 0; }
}

