.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1200px;
  margin-block: 2rem;
  grid-gap: 1rem;
  gap: 1rem;
}

img {
  max-width: 100%;
  display: block;
  object-fit: cover;
}


.card {
  /* z-index: -1; */
  display: flex;
  flex-direction: column;
  width: clamp(22rem, calc(22rem + 2vw), 24rem);
  overflow: hidden;
  box-shadow: 0 .1rem 1rem rgba(130, 199, 211, 0.1);
  border-radius: 1em;
  /* background: linear-gradient(to right, #FFFFFF, #ffffff); */
  background-color: #fafbff;
  margin-bottom: 2%;

}
.card:hover{
  border-bottom: 2px solid rgb(7, 158, 196);
}


.card__body {
  padding: 1rem 1rem .5rem 1rem;
  display: flex;
  flex-direction: column;
  grid-gap: .3rem;
  gap: .3rem;
}





.card__body h4 {
  font-size: 1.5rem;
  text-transform: capitalize;
}

.card__footer {
  display: flex;
  padding: .5rem 1rem 1rem 1rem;
  margin-top: auto;

}

.user {
  display: flex;
  grid-gap: .5rem;
  gap: .5rem;
}

.user__image {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.user__info > small {
  color: #666;
}
