#about{
    height: 100vh;
    width: 100%;
    background-color: #ffffff;
    justify-content: center;
    align-items: center;
    align-content: center;
    padding: 1%;
    margin: 0%;
}

.about .content {
    background-color: #fafbff;
    padding: 40px;
  }
  .about h3 {
    font-size: 14px;
    font-weight: 700;
    color: #4154f1;
    text-transform: uppercase;
  }
  .about h2 {
    font-size: 24px;
    font-weight: 700;
    color: #012970;
  }
  .about p {
    margin: 15px 0 30px 0;
    line-height: 24px;
  }
  .about .btn-read-more {
    line-height: 0;
    padding: 15px 40px;
    border-radius: 4px;
    transition: 0.5s;
    color: #fff;
    background: #4154f1;
    box-shadow: 0px 5px 25px rgba(65, 84, 241, 0.3);
    text-decoration: none;
  }
  .about .btn-read-more span {
    font-family: "Nunito", sans-serif;
    font-weight: 600;
    font-size: 16px;
    letter-spacing: 1px;
  }
  .about .btn-read-more i {
    margin-left: 5px;
    font-size: 18px;
    transition: 0.3s;
  }
  .about .btn-read-more:hover i {
    transform: translateX(5px);
  }
  